}

sup {
  top: -.8em;
}

a {
  color: #2196F3;
  text-decoration: none;
  background-color: transparent;
}

a:hover {
  color: #010c14;
  text-decoration: underline;
}

a:not([href]):not([tabindex]) {
  color: inherit;
  text-decoration: none;
}

